    padding-top: 7.28266px;
    padding-bottom: 7.28266px;
    padding-left: 7.28266px;
    padding-right: 7.28266px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    cursor: pointer;
    transition: background-color 0.3s ease-out;
    background-color: rgba(255, 255, 255, 0); }
    @media (min-width: 576px) {
      .topic-list li {
        padding-top: calc( 7.28266px + -1.91191 * (100vw - 576px) / 1024); } }
    @media (min-width: 1600px) {
      .topic-list li {
        padding-top: 5.37075px; } }
    @media (min-width: 576px) {
      .topic-list li {
        padding-bottom: calc( 7.28266px + -1.91191 * (100vw - 576px) / 1024); } }
